<template>
  <div class="login_container">
    <div class="login_box">
      <!-- avatar area -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!-- login form area -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- user name -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.name"
            prefix-icon="el-icon-user"
          ></el-input>
        </el-form-item>
        <!-- password -->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.pwd"
            prefix-icon="el-icon-lock"
            type="password"
          ></el-input>
        </el-form-item>
        <!-- login button -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login">Login</el-button>
          <el-button type="info" @click="resetLoginForm">Reset</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    // data binding
      loginForm: {
        name: 'root',
        pwd: '123456',
        perms:"user-add"
      },
      // data validation
      loginFormRules: {
        name: [
          { required: true, message: 'Please input username', trigger: 'blur' },
          { min: 3, max: 10, message: 'length between 3 to 10 chars', trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: 'Please input password', trigger: 'blur' },
          { min: 3, max: 20, message: 'length between 3 to 20 chars', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // reset login form
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const res  = await this.$http.post('user/login', this.loginForm)
        if (!res.data.token) return this.$message.error('Fail to login')
        this.$message.success('Successfully login')
        console.log(res)
        //1.sotre token into sessionStorage
        if(res.data.token){
           window.sessionStorage.setItem('token', res.data.token)
          // //2. programmically jump to home page
          this.$router.push('/home')
        }
       
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
